/*  <fonts>  */
@font-face {
  font-family: 'Franklin Gothic Book';
  src: url("../fonts/FranklinGothic-Book.woff2") format('woff2'), url("../fonts/FranklinGothic-Book.woff") format('woff');
  font-weight: normal;
  font-style: normal;
}
/*  </fonts>  */
/*  <vars>  */
/*  </vars>  */
/*  <general>  */
html {
  font-size: 62.5%;
  cursor: default;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  -webkit-tap-highlight-color: #000;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
body {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  color: #001b35;
  font: normal 1.4rem/1.6 'Franklin Gothic Book';
  background: #fff url("../i/bg.jpg") no-repeat 0 0/cover;
}
:-moz-placeholder {
  opacity: 1;
  color: currentColor;
}
:-ms-input-placeholder {
  opacity: 1;
  color: currentColor;
}
a {
  color: #5769a3;
  font: inherit;
  text-decoration: none;
  transition: all 0.2s;
}
a:hover {
  text-decoration: underline;
}
img {
  max-width: 100%;
}
.main {
  flex: 1;
}
.no-scroll {
  overflow: hidden;
}
/*  </general>  */
/*  <header>  */
.header {
  flex-shrink: 0;
  padding: 16px 0;
}
.header .container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header__menu {
  position: relative;
  width: 33px;
  height: 25px;
}
.header__menu::before,
.header__menu::after {
  position: absolute;
  right: 0;
  left: 0;
  height: 4px;
  background-color: #f24941;
  border-radius: 3px;
  content: '';
}
.header__menu::before {
  top: 0;
}
.header__menu::after {
  top: 50%;
  transform: translateY(-50%);
}
.header__menu span {
  position: absolute;
  right: 0;
  bottom: 0;
  width: 22px;
  height: 4px;
  background-color: #f24941;
  border-radius: 3px;
  transition: all 0.2s;
}
a:hover.header__menu span {
  width: 100%;
}
/*  </header>  */
/*  <nav>  */
.nav {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  z-index: 9999;
  overflow-y: auto;
  width: 418px;
  background: rgba(255,255,255,0.7) url("data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAQAAAAEAQMAAACTPww9AAAABlBMVEX///////9VfPVsAAAAAXRSTlOzEo46UAAAAA5JREFUCNdjWMAgAMQOAAbYAZE1p3R9AAAAAElFTkSuQmCC");
  transform: translateX(100%);
}
@media screen and (max-width: 769px) {
  .nav {
    width: 380px;
  }
}
@media screen and (max-width: 380px) {
  .nav {
    width: 90%;
  }
}
.nav--active {
  animation: showMenu 1s;
  transform: translateX(0);
}
.nav--hide {
  animation: hideMenu 1s;
  transform: translateX(0);
}
.nav__cover {
  animation: fadeIn 0.5s;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 9998;
  background-color: rgba(0,0,0,0.38);
}
.nav-wrap {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  height: 100%;
  padding: 155px 130px 0 70px;
}
@media screen and (max-width: 769px) {
  .nav-wrap {
    padding: 100px 100px 0 60px;
  }
}
@media screen and (max-width: 380px) {
  .nav-wrap {
    padding: 70px 70px 0 60px;
  }
}
.nav-main {
  display: flex;
  flex-direction: column;
}
.nav-main__item {
  align-self: flex-start;
  color: #001b35;
  font-size: 2.4rem;
  margin: 10px 0;
}
a:hover.nav-main__item {
  color: #f24941;
  text-decoration: none;
}
.nav-main__item--active {
  position: relative;
  color: #f24941;
}
.nav-main__item--active::before {
  position: absolute;
  top: 50%;
  left: -22px;
  width: 8px;
  height: 8px;
  background-color: #f24941;
  border-radius: 50%;
  content: '';
  transform: translateY(-50%);
}
.nav-phone {
  align-self: flex-start;
  color: #f24941;
  font-weight: bold;
  font-size: 1.8rem;
  margin: 20px 0;
}
a:hover.nav-phone {
  text-decoration: none;
}
.nav-address {
  color: #58717d;
  font-style: normal;
  font-size: 1.4rem;
  line-height: 1.8rem;
}
.nav-copyright {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  margin: 85px 0;
}
/*  </nav>  */
/*  <homepage>  */
.homepage {
  max-width: 600px;
  margin: 120px 0;
}
.homepage__asphalting {
  color: #1e0e00;
  font-size: 1.8rem;
  margin: 0 0 45px 30px;
}
.homepage__asphalting span {
  color: #be0d1b;
}
.homepage__title {
  font-size: 6rem;
  line-height: 1.3;
}
.homepage__notice {
  font-size: 1.8rem;
  margin: 20px 0 80px;
}
.homepage__details {
  display: flex;
  align-items: center;
}
.homepage__details-more {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 46px;
  color: #fff;
  font-size: 1.8rem;
  background-color: #001b35;
  border-radius: 4px;
  padding: 0 36px;
  margin-right: 14px;
}
a:hover.homepage__details-more {
  text-decoration: none;
  background-color: #003568;
}
.homepage__details-view {
  color: #001b35;
  font-size: 1.8rem;
}
a:hover.homepage__details-view {
  text-decoration: none;
}
.homepage__details-view::after {
  animation: slide 1s infinite;
  font-family: 'Sans-serif';
  margin-left: 5px;
  content: '\2192';
}
/*  </homepage>  */
/*  <modal>  */
.modal {
  display: none;
  position: relative;
  max-width: 1200px;
  background-repeat: no-repeat;
  background-size: cover;
  border: 3px solid #fff;
  border-radius: 8px;
}
.modal::before {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 3;
  background-color: rgba(255,255,255,0.7);
  content: '';
  pointer-events: none;
}
.modal-header,
.modal-content,
.modal-footer {
  position: relative;
  z-index: 4;
}
.modal.fancybox-content {
  padding: 40px 45px 24px;
}
.modal .fancybox-close-small {
  display: none;
}
.modal__close {
  position: absolute;
  top: 16px;
  right: 24px;
  z-index: 5;
  width: 49px;
  height: 49px;
  background-color: #f24941;
  border-radius: 50%;
}
a:hover.modal__close {
  background-color: #ef1b11;
}
.modal__close::before,
.modal__close::after {
  position: absolute;
  top: 50%;
  left: 25px;
  width: 40px;
  height: 4px;
  background-color: #fff;
  border-radius: 4px;
  margin-top: -2px;
  margin-left: -21px;
  content: '';
  pointer-events: none;
}
.modal__close::before {
  transform: rotate(45deg);
}
.modal__close::after {
  transform: rotate(-45deg);
}
.modal-header {
  display: flex;
  align-items: center;
}
@media screen and (max-width: 769px) {
  .modal-header {
    flex-direction: column;
    align-items: flex-start;
  }
}
.modal-header__logo {
  flex-shrink: 0;
  margin-right: 35px;
}
@media screen and (max-width: 769px) {
  .modal-header__logo {
    margin-right: 0;
    margin-bottom: 15px;
  }
}
.modal-header__logo img {
  vertical-align: top;
}
.modal-header__info {
  width: 200px;
}
.modal-header__info-phone {
  color: #f24941;
  font-weight: bold;
  font-size: 1.8rem;
  line-height: 1;
}
a:hover.modal-header__info-phone {
  text-decoration: none;
}
.modal-header__info-address {
  color: #58717d;
  font-style: normal;
  line-height: 1;
  margin-top: 10px;
}
.modal-content {
  margin: 200px 0;
}
@media screen and (max-width: 769px) {
  .modal-content {
    margin: 30px 0;
  }
}
.modal-content__title {
  font-size: 3rem;
  text-decoration: underline;
}
.modal-content__text {
  font-size: 1.8rem;
}
.modal-footer {
  text-align: center;
}
@media screen and (max-width: 769px) {
  .modal-footer {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
}
.modal-footer__link {
  color: #001b35;
  text-transform: uppercase;
  text-decoration: underline;
  margin: 0 20px;
}
@media screen and (max-width: 769px) {
  .modal-footer__link {
    width: 40%;
    margin: 5px 0;
  }
}
a:hover.modal-footer__link {
  text-decoration: none;
}
.fancybox-is-open .fancybox-bg {
  opacity: 0.1;
}
/*  </modal>  */
